.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  background: #FCFDFE;
  overflow-y: scroll;
  --secondary: rgb(161, 161, 161);
  --divider: #343434;
  -webkit-font-smoothing: antialiased;
}

* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#root {
  display: flex;
  justify-content: center;
}

.screen {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 1280px;
  padding: 45px 25px;
  display: flex;
  flex-direction: row;
}

h1 {
  font-weight: 600;
  color: #1A1F36;
  margin: 6px 0 12px;
  font-size: 27px;
  line-height: 32px;
}

h1 span.light {
  color: #A3ACB9;
}

.date {
  color: var(--secondary);
  font-size: 14px;
  text-transform: uppercase;
}

header {
  position: relative;
  flex: 0 0 250px;
  padding-right: 48px;
}

.header-content {
  position: sticky;
  top: 45px;
}

.logo img {
  height: 20px;
  margin-bottom: 52px;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  padding-top: 64px;
}

.card {
  position: relative;
  padding: 12px;
  height: 320px;
  flex: 0 0 33%;
  min-width: 304px;
  width: 33%;
}

.card-content-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
  pointer-events: none;
}

.card-content-container.open {
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 1;
  overflow: hidden;
  padding: 40px 0;
}

.card-content {
  pointer-events: auto;
  position: relative;
  border-radius: 20px;
  background: #1c1c1e;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  box-shadow: 0 2px 5px 0 rgba(50, 50, 93, 0.10), 0 1px 1px 0 rgba(0, 0, 0, 0.07);

}

.open .card-content {
  height: auto;
  max-width: 790px;
  overflow: hidden;
}

.card-open-link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.card-image-container {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 220px;
  width: 100%;
  max-width: 304px;
  display: flex;
  justify-content: center;
  transform: translateZ(0);
}

.card-demo-container {
  width: 721px;
  background: rgba(255, 255, 255, 0.4);
  box-shadow: 0 2px 5px 0 rgba(50, 50, 93, 0.10), 0 1px 1px 0 rgba(0, 0, 0, 0.07);
  border-radius: 6px;
  position: absolute;
  top: 110px;
  height: 363px;
  overflow: hidden;
}

.card-demo-loader-container {
  position: absolute;
  top: 2px;
  left: 3px;
  height: 3px;
  width: calc(100% - 6px);
  z-index: 9;
}

.card-demo-loader {
  width: 0;
  height: 3px;
  border-radius: 3px;
  background-color: #C1C9D2;
}

.demo-frame {
  width: 980px;
  height: 493px;
  border: 0;
  border-radius: 6px;
  background: white;
  transform: scale(.735714286);
  transform-origin: 0 0;
}

.demo-frame .sample-info {
  display: none;
}

.cli-command {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  backdrop-filter: blur(10px);
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.cli-command-content {
  width: 50%;
  text-align: center;
}

.cli-command-content h4 {
  margin-bottom: 8px;
}

.cli-command-content p {
  line-height: 1.2;
  color: rgba(255, 255, 255, 0.7);
}

.cli-command code {
  font-family: Menlo, monospace;
  font-size: 13px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 6px;
  padding: 6px;
  margin-top: 12px;
  display: block;
}

.cli-command a {
  font-weight: 600;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.title-container {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 240px;
}

h2 {
  color: #fff;
  margin: 8px 0;
}

h3 {
  font-size: 17px;
  color: #fff;
  margin: 8px 0;
}

.category {
  color: #fff;
  font-size: 14px;
  opacity: 0.7;
}

.overlay {
  z-index: 1;
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  will-change: opacity;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

.overlay a {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100vw;
  left: 50%;

  transform: translateX(-50%);
}

.content-container {
  padding: 500px 35px 35px 35px;
  max-width: 700px;
  width: 90vw;
  display: flex;
}

.content-container h4,
.content-container h5 {
  color: #fff;
  font-size: 13px;
  margin-bottom: 8px;
  opacity: 0.7;
  font-size: 12px;
  font-weight: 600;

}

.content-container h4 {
  text-transform: uppercase;
}

.content-container p {
  color: #fff;
  font-size: 13px;
  line-height: 17px;
  margin-top: 0;
  margin-bottom: 4px;
  opacity: 0.8;
}

.content-body {
  flex: 0 0 260px;
  padding-right: 32px;
}

.test-card-row {
  display: flex;
  align-items: center;
}

.test-card-row p {
  margin: 0;
}

.link-button {
  background: none;
  color: white;
  text-transform: uppercase;
  appearance: none;
  border: none;
  font-weight: 600;
  outline: none;
  cursor: pointer;
}

.title-buttons {
  display: flex;
  width: 100vh;
}

.icon-button {
  padding-right: 16px;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 4px 6px;
  margin-top: -6px;
  margin-left: -6px;
  margin-right: 16px;
  border-radius: 6px;

  transition: background 0.2s ease;
}

.icon-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.icon-button-icon {
  left: 0;
  top: 3px;
}

.icon-button-text {
  font-weight: 600;
  font-size: 13px;
  color: #F7FAFC;
  padding-left: 6px;
}


@media only screen and (max-width: 980px) {
  .container {
    flex-direction: column;
  }

  .header-content {
    max-width: 280px;
    position: relative;
    top: 0;
  }

  .card-list {
    padding-top: 0;
  }

  .card-demo-container {
    width: calc(100% - 64px);
  }

  .demo-frame {
    width: 100%;
    transform: none;
  }

}

@media only screen and (max-width: 600px) {
  .container {
    flex-direction: column;
  }

  .card {
    flex: 1 0 100%;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
  }

  .card-demo-container {
    width: calc(100% - 64px);
  }

  .demo-frame {
    width: 100%;
    transform: none;
  }

  .card-list {
    padding-top: 0;
  }

  .card-image-container {
    max-width: 100%;
  }

  .card-content-container.open {
    padding: 0;
  }

  .content-container {
    flex-direction: column;
  }

  .content-body {
    flex: 0 0 auto;
  }

  .cli-command-content {
    width: 80%;
  }
}